footer {
  margin-top: 2rem;
  padding: 0 0.5rem 2rem 0.5rem;
  background-color: #333;
  color: #f2f2f2;
  font-size: 0.8em;
}

.contact {
  width: 80%;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
  margin-bottom: 1rem;
}

.contact > h2 {
  border: none;
  font-size: 0.9em;
  padding: 0;
  margin: 0;
}

.contact > a {
  padding: 0.5rem;
}

.icons {
  height: 30px;
}

.copyright {
  text-align: center;
}

.map {
  width: 100%;
  height: 40vmin;
  border: 0;
}
